.agenda-section {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  /* background-color: #f2f1eb; */
  background-color: #e0dfd6;
  /* border: 1px solid red; */
}

.agenda-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  overflow-y: auto;
  /* border: 1px solid blue; */
}

.agenda-header {
  min-height: 30vh;
  flex-shrink: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* margin: 0.25rem; */
  border: 1px solid red;
}
.agenda-header h3 {
  /* padding: 1rem; */
  margin: 1rem;
  padding: 0.5rem;
  font-size: 2rem;
  font-weight: bold;
  color: #4f4b45;
}
.agenda-header button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin: 0 0.75rem;
  border-color: transparent;
  background-color: transparent;
  font-size: 1.3rem;
  /* color: rgba(0, 0, 0, 0.7); */
  color: #a9a397;
  cursor: pointer;
}
.agenda-header button:hover {
  color: rgba(0, 0, 0, 1);
}
.agenda-calendar {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
  position: relative;
  /* border: 1px solid rgba(0, 0, 0, 3); */
}
.agenda-status {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
  /* background-color: lightgray; */
  background-color: #f2eee7;
  color: #4f4b45;
}
/*수정 다시*/
.calendar-container {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
}
.calendar-weekdays {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #fff;
  background-color: #ede7dd;
  background-color: #e0d6c5;
  color: #a9a397;
}
.calendar-week {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  padding: 0.5rem;
  font-size: 1.1rem;
  /* color: #fff; */
  color: #4f4b45;
}

