.calendar-days-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, minmax(80px, 1fr));
  background-color: #f7f7f7;
}
.calendar-day-btn {
  position: relative;
  display: flex;
  padding: 0.5rem 0.55rem;
  font-size: 1.1rem;
  font-weight: bold;
  background-color: #f2eee7;
  background-color: #f8f3e4;
  border: 1px solid rgba(0, 0, 0, 0.2);
  color: #4f4b45;
  cursor: pointer;
}
.calendar-day-btn.activeDay {
  /* background-color: orange; */
  /* background-color: #4caf50; */
  /* background-color: red; */
  /* background-color: #D5C8B6; */
  background-color: #bfaf9b;
}
.calendar-day-btn:hover {
  background-color: #d5c8b6;
}
.agenda-day-underline {
  position: absolute;
  top: 70%;
  left: 0;
  right: 0;
  width: 100%;
  height: 15%;
}
.completedDay .agenda-day-underline {
  background-color: #a3a3a3;
}
.incompleteDay .agenda-day-underline {
  background-color: #d08b5b;
}
/*오늘 요일 표시*/
.activeDay .agenda-day-underline {
  background-color: #c75c44;
}
/*오늘 완료 표시*/
.activeDay.completedDay .agenda-day-underline {
  background-color: black;
}
@media screen and (max-width: 479px) {
  .calendar-day-btn {
    /* padding: 0.5rem 0.45rem; */
    padding: 0.5rem 0.35rem;
    /* font-size: 0.9rem; */
    font-size: 1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .calendar-day-btn {
    /* padding: 0.5rem; */
    padding: 0.5rem 0.45rem;
    /* font-size: 0.95rem; */
    font-size: 1.05rem;
  }
}

@media screen and (min-width: 768px) {
  .calendar-day-btn {
    padding: 0.5rem 0.55rem;
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1024px) {
  .calendar-day-btn {
    padding: 0.5rem 0.65rem;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1280px) {
  .calendar-day-btn {
    padding: 0.5rem 0.75rem;
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1440px) {
  .calendar-day-btn {
    padding: 0.5rem 0.85rem;
    font-size: 1.4rem;
  }
}
