.today-date-time-center {
  /* flex: 0 0 30%; */
  flex: 0 0 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  /* background-color: #f9f9f9; */
  /* background-color: #4a4a4a; */
  /* background-color: #6d6a5e; */
  color: #333;
  /* border: 1px solid blue; */
}
.today-date-time-center h2 {
  font-size: 2rem;
  font-size: 1.5rem;
  color: #000;
  text-align: center;
}

@media screen and (max-width: 479px) {
  .today-date-time-center {
    flex: 0 0 5vh;
    padding: 0.5rem;
    margin: 0.75rem 0.5rem;
  }
  .today-date-time-center h2 {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .today-date-time-center {
    flex: 0 0 5vh;
    padding: 0.5rem;
    /* margin: 0.25rem 0.5rem; */
    margin: 0.75rem 0.5rem;
  }
  .today-date-time-center h2 {
    font-size: 1.6rem;
  }
}

