.home-section {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #e0dfd6;
  overflow: hidden;
  /* border: 1px solid red; */
}
.home-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0.25rem 0.5rem;
  overflow-y: auto;
  /* border: 1px solid blue; */
}
.home-title {
  min-height: 30vh;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid red;
}
.home-title h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem;
  font-size: 1.3rem;
}
.preview-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0;
  padding: 0.5rem 0;
  gap: 1rem;
}
.preview-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 0.25rem;
  background-color: #fff;
  color: #6d6a5e;
}
.preview-title {
  display: flex;
  align-items: center;
  padding: 0.5rem 0.25rem;
  background-color: #f0f0f0;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
}
.preview-title h4 {
  padding: 0.5rem;
  font-size: 1.3rem;
  margin-right: 0.5rem;
}
.preview-summary {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: 0.5rem;
  padding: 0.5rem 0;
  font-size: 1rem;
  font-weight: bold;
}
.preview-summary span {
  margin: 0 0.5rem;
}
.preview-center {
  flex: 1;
  /* border: 1px solid red; */
}
.preview-todo-list,
.preview-agenda {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /* grid-auto-rows: minmax(100px, auto); */
  grid-auto-rows: 150px;
  gap: 0.5rem;
  padding: 0.25rem;
  word-break: break-word;
  background-color: #f2f1eb;
}
.preview-todo-list li,
.preview-agenda li {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  font-size: 1.2rem;
  background-color: #e8e3da;
  border-radius: 0.25rem;
  color: #6d6a5e;
  overflow-y: auto;
  list-style-type: decimal;
  box-shadow: 1px 2px 0 rgba(0, 0, 0, 0.3);
  overflow: hidden;
  overflow-y: auto;
}
.agenda-mini-date {
  margin-bottom: 0.25rem;
}
.preview-more,
.preview-add {
  padding: 0.5rem;
  font-size: 1.1rem;
  font-weight: bold;
  background-color: #f0f0f0;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}
.empty-preview {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 30vh;
  padding: 0 0.5rem;
  font-weight: bold;
}
.empty-preview p {
  margin: 2rem;
  padding: 0.5rem;
  font-size: 1.2rem;
}
.agenda-current-date {
  padding: 0.15rem 0.75rem;
  font-size: 1rem;
  background-color: #f0f0f0;
  border-top-right-radius: 0.25rem;
  border-top-left-radius: 0.25rem;
}
@media screen and (max-width: 479px) {
  .home-container {
    padding: 0.25rem;
    margin: 0.25rem 0.5rem;
  }
  .home-title {
    min-height: 10vh;
    padding: 0.25rem;
    margin: 0.25rem;
  }
  .home-title h3 {
    padding: 0.25rem;
    margin: 0.25rem;
    /* font-size: 1.1rem; */
    font-size: 1.5rem;
  }

  .preview-wrapper {
    margin: 0.25rem 0.25rem;
  }

  .preview-title h4 {
    font-size: 1.2rem;
    /* border: 1px solid red; */
  }
  .preview-summary {
    margin-left: 0.25rem;
    padding: 0.25rem 0;
    font-size: 0.9rem;
  }
  .preview-summary span {
    margin: 0 0.25rem;
  }
  .preview-todo-list,
  .preview-agenda {
    grid-template-columns: 1fr;
    /* grid-auto-rows: minmax(90px, 100px); */
    grid-auto-rows: 100px;
    /* gap: 0.5rem; */
    padding: 0.25rem;
  }
  .preview-todo-list li,
  .preview-agenda li {
    font-size: 1rem;
  }
  .preview-more,
  .preview-add {
    padding: 0.25rem 0.5rem;
    font-size: 1rem;
  }
  .empty-preview p {
    font-size: 1rem;
  }
  .agenda-current-date {
    font-size: 0.9rem;
  }
}

