.menu-buttons-center {
  flex: 1;
  padding: 0.25rem;
  background-color: #f2f1eb;
  border: 1px solid blue;
}

.menu-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 0.25rem;
  background-color: #f2f1eb;
}
.menu-list li {
  width: 100%;
  margin: 0.5rem;
}
.menu-btn {
  width: 100%;
  border-color: transparent;
  background-color: #c8d5b9;
  color: #5b5f52;
  border-radius: 0.25rem;
  font-weight: bold;
  cursor: pointer;
}
.menu-btn:hover {
  /* color: #fff; */
  background-color: #b8c4a5;
}
@media screen and (max-width: 479px) {
  .menu-buttons-center {
    padding: 0;
    margin: 0.25rem 0.5rem;
    overflow: hidden;
    /* border: 1px solid blue; */
  }
  .menu-list {
    flex-direction: row;
    width: 100%;
    padding: 0.25rem 0;
    overflow-x: auto;
  }
  .menu-list li {
    margin: 0 0.25rem;
    /* margin: 0; */
    /* border: 1px solid red; */
  }
  .menu-btn {
    padding: 0.5rem;
    font-size: 1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .menu-buttons-center {
    padding: 0;
    margin: 0.25rem 0.5rem;
    overflow: hidden;
  }
  .menu-list {
    flex-direction: row;
    width: 100%;
    padding: 0.25rem 0;
    overflow-x: auto;
  }
  .menu-list li {
    margin: 0 0.25rem;
  }
  .menu-btn {
    padding: 0.5rem;
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 768px) {
  .menu-btn {
    padding: 0.5rem;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1024px) {
  .menu-btn {
    padding: 0.75rem;
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1280px) {
  .menu-btn {
    padding: 1rem;
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1440px) {
  .menu-btn {
    padding: 1.25rem;
    font-size: 1.5rem;
  }
}
