.agenda-list-container {
  flex: 1;
  width: 100%;
  overflow-y: auto;
  margin-bottom: 0.5rem;
  background-color: #f2f1eb;
}
.agenda-list-center {
  width: 100%;
  min-height: 100%;
  padding: 0 0.25rem;
  /* background-color: #fff; */
}

.agenda-item {
  display: flex;
  padding: 0.5rem;
  margin: 0.5rem 0.25rem;
  background-color: #e8e3da;
  border-radius: 0.25rem;
  /* border: 1px solid red; */
  justify-content: center;
  align-items: center;
}
.form-agenda-input {
  flex: 1;
  display: flex;
}
.input-checkobx {
  margin-right: 1rem;
  color: #c8d5b9;
}

.agenda-content {
  flex: 1;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-radius: 0.25rem;
  /* background-color: #fff; */
  word-break: break-word;
}

@media screen and (max-width: 479px) {
  .agenda-list-center {
    padding: 0 0.25rem;
    /* border: 1px solid red; */
  }
  .agenda-item {
    flex-direction: column;
    align-items: flex-start;
  }
  .form-agenda-input {
    flex: 1;
    width: 100%;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .agenda-list-center {
    padding: 0 0.25rem;
    /* border: 1px solid red; */
  }
  .agenda-item {
    flex-direction: column;
    align-items: flex-start;
  }
  .form-agenda-input {
    flex: 1;
    width: 100%;
  }
}

