.todo-section {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* background-color: #f2f1eb; */
  /* background-color: #d6c7b7; */
  background-color: #e0dfd6;
  /* border: 1px solid red; */
}
.todo-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  overflow-y: auto;
  /* border: 1px solid blue; */
}
.todo-title {
  flex-shrink: 0;
  min-height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem;
  border: 1px solid red;
  /* background-color: #f2eee7; */
}
.todo-title h3 {
  padding: 1rem;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  color: #4f4b45;
}

.todo-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0.25rem 0.5rem;
  /* border: 1px solid red; */
}
.form-control {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0.25rem 0;
  /* border: 1px solid blue; */
}
.todo-input {
  flex: 1;
  padding: 0.5rem 0.75rem;
  margin-right: 0.5rem;
  /* color: #5b5f52; */
  /* background-color: #fff; */
  /* border: 1px solid #c8d5b9; */

  color: #4f4b45;
  background-color: #f2eee7;
  border: 1px solid #b8b2a7;
  border-radius: 0.25rem;
  font-size: 1.1rem;
}
.todo-input:focus {
  background-color: #ece8e1;
  border: 1px solid #a9a397;
}
.todo-input::placeholder {
  font-size: 1rem;
  color: #5b5f52;
}
.submit-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  margin-right: 0.5rem;
  background-color: transparent;
  border-color: transparent;
  color: #5b5f52;
  /* background-color: #c8d5b9; */
  font-weight: bold;
  border-radius: 0.25rem;
  font-size: 1.1rem;
  cursor: pointer;
}
.submit-btn:hover {
  /* color: #000; */
  color: #3e3b36;
}
.clear-btn {
  margin: 0.25rem;
  padding: 0.5rem;
  background-color: #b8b2a7;
  border-color: transparent;
  border-radius: 0.25rem;
  color: #4f4b45;
  font-size: 1.1rem;
  font-weight: bold;
  letter-spacing: 0.15rem;
  cursor: pointer;
}
.clear-btn:hover {
  background-color: #a9a397;
  color: #3e3b36;
}
.todo-empty-message {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem;
  height: 100%;
  font-size: 1.4rem;
  font-weight: bold;
  /* background-color: #f5f5f5; */
  /* background-color: #f8d7da; */

  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
}
.todo-empty-message p {
  padding: 0.5rem;
}
